 .wp-travel-advanced-gallery-items-list {
    display: flex;
    flex-wrap: wrap;
    list-style: none;
    padding: 0 !important;
}

.alignfull .wp-block-wp-travel-blocks-trip-gallery,
.alignfull #wptravel-block-trip-gallery{
    max-width: 100% !important;
}


.wptravel-block-trip-gallery{
    .slick-dots{
        bottom: 0px;
    }
    .slick-next {
        right: 60px !important;
    }
    .slick-prev {
        left: 40px !important;
        z-index: 10;
    }
}

.wptravel-block-trip-gallery.slick-next:before, .wptravel-block-trip-gallery.slick-prev:before {
    font-size: 30px;
}

.wptravel-block-trip-gallery .slick-dots li button:before {
    position: absolute;
    top: 0;
    left: 0;
    content: "â€¢";
    width: 20px;
    height: 20px;
    font-size: 16px;
    line-height: 20px;
    text-align: center;
    color: #fff;
    opacity: .25;
}

#wptravel-block-trip-gallery {
    &.wptravel-block-trip-gallery {
        &.slider {
            .wp-travel-advanced-gallery-items-list {
                &.slick-slider {
                    .item {
                        padding: 0;
                    }
                }

                .slick-next, .slick-prev, .slick-dots {
                    &:before {
                        font-family: "Font Awesome 5 Free";
                        padding: 5px 10px 10px 10px;
                        font-size: 20px;
                        border: 1px solid #FFF;
                    }
                }

                .slick-next {
                    right: 40px;
                }

                .slick-list {
                    .slick-track {
                        display: flex;
                    }
                }

                .slick-dots {
                    bottom: 10px;

                    li {
                        &.slick-active {
                            button {
                                opacity: 1;
                            }
                        }

                        button {
                            background-color: black;
                            opacity: 0.25;
                            height: 10px;
                            width: 13px;

                            &:before {
                                content: "";
                            }
                        }
                    }
                }
            }
        }
    }
}

.wp-travel-advanced-gallery-items-list .slick-arrow:before{
    background-color: #000 !important;
    border-color: #000 !important;
    color: #fff !important;
}

#wptravel-block-trip-gallery.wptravel-block-trip-gallery button:focus,
#wptravel-block-trip-gallery.wptravel-block-trip-gallery button:hover{
    background-color: transparent !important;
}

.slick-track div{
    padding-right: 5px;
    padding-left: 10px;
}

.slick-track div:first-child{
    padding-left: 5px;
}

